import React, { Component } from 'react';
import giphy from 'giphy-api';
import SearchBar from "./search_bar.jsx";
import GifList from "./giflist.jsx";
import Gif from "./gif.jsx";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      gifs: [],
      selectedGifId: '1yTcuT8dXngxKAH7QC'
    };
    this.search('homer thinking');
  }

  search = (query) => {
    giphy('Pvw63B6n65rbmi1fbjUayTaHwRx4QD6f').search(query,
      (err, res) => {
        this.setState({ gifs: res.data });
      });
  }

  updateSelectedGif = (id) => {
    this.setState({
      selectedGifId: id
    })
  }

  render() {
    return (
      <div>
        <div className="left-scene">
          <SearchBar search={this.search} />
          <div className="selected-gif">
            <Gif id={this.state.selectedGifId} />
          </div>
        </div>
        <div className="right-scene">
          <GifList gifs={this.state.gifs} updateSelectedGif={this.updateSelectedGif}/>
        </div>
      </div>
    )
  }


}

export default App;
